import type { Action } from '@ali/4ever-cangjie';

export const QUICK_INSERT_INSERT = Symbol('insert-quick-insert');
export const QUICK_INSERT_SHOW = Symbol('show-quick-insert');
export const QUICK_INSERT_SET = Symbol('set-quick-insert');
export const QUICK_INSERT_RESET = Symbol('reset-quick-insert');
export const QUICK_INSERT_REMOVE = Symbol('remove-quick-insert');

export interface SetQuickInsertPayload {
  plugin: string;
}

export interface ResetQuickInsertPayload {
  plugin: string;
}

export function insertQuickInsert(plugin: string): Action<{ plugin: string }> {
  return {
    type: QUICK_INSERT_INSERT,
    payload: { plugin },
  };
}

export function showQuickInsert(): Action<{}> {
  return {
    type: QUICK_INSERT_SHOW,
    payload: {},
  };
}

export function setQuickInsert(plugin: string): Action<SetQuickInsertPayload> {
  return {
    type: QUICK_INSERT_SET,
    payload: {
      plugin,
    },
  };
}

export function resetQuickInsert(
  plugin: string,
): Action<ResetQuickInsertPayload> {
  return {
    type: QUICK_INSERT_RESET,
    payload: { plugin },
  };
}

export function removeQuickInsert(): Action<{}> {
  return {
    type: QUICK_INSERT_REMOVE,
    payload: {},
  };
}
